function MainView(props){
  const switcher=(e)=>{
    let neoModal={...props.userPack.modal}
    neoModal.active=!neoModal.active
    props.userPack.methods.stateHandling('modal',neoModal)
  }
    return(
      <>
        <Modal switcher={switcher} active={props.userPack.data.modal.active}>

        </Modal>
        <WhiteNavBar userPack={props.userPack}/>
        <Body userPack={props.userPack}/>
        <Signature userPack={props.userPack}/>
        {props.children}
      </>
    )
  }

  export default MainView;